.Skills {
    margin: 20px;
    margin-left: 80px;
    display: flex;
    align-items: center;
    flex-direction: row;
}

.icon {
    width: 10%;
    height: 80px;
    text-align: center;
    color: #333;
}

.bar {
    width: 80%;
    height: 40px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 5px 5px 10px rgb(172, 172, 172);
    overflow: hidden;
    margin-left: 5%;
}

.skillbar {
    height: 40px;
    background-color: rgb(70, 37, 255);
    border-radius: 10px;
    box-shadow: 5px 0px 10px #ccc;
}

@media (max-width: 900px) {
    .icon {
        font-size: 10px;
        height: 50px;
    }
    .bar {
        width: 70%;
        height: 30px;
    }
    .skillbar {
        height: 30px;
    }
}

@media (max-width: 650px) {
    .Skills {
        margin-left: 40px;
        margin-right: 10px;
    }
    .icon {
        font-size: 10px;
        height: 50px;
    }
    .bar {
        width: 70%;
        height: 20px;
        margin-left: 10%;
    }
    .skillbar {
        height: 20px;
    }
}

@media (max-width: 350px) {
    .Skills {
        margin-left: 20px;
    }
}