} */

@media (max-width: 900px) {
    .Navitem a {
        padding: 16px 16px;
    }
}

@media (max-width: 500px) {
    .Navitem {
        padding-top: 16px;
        font-size: 16px;
    }
    .Navitem a {
        padding: 10px 10px;
    }
}

@media (max-width: 355px) {
    .Navitem {
        padding-top: 18px;
        font-size: 14px;
    }
    .Navitem a {
        padding: 8px 8px;
    }
}