.bgc {
    background-image: url('./assets/image/Light-Blue-Ocean.jpg');
    width: 100%;
    height: auto;
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
}

@media (min-width: 1367px) {
    .bgc {
        width: 100%;
        height: auto;
    }
}

@media (min-height: 658px) {
    .bgc {
        width: auto;
        height: 100%;
    }
}